import { BrowserModule } from '@angular/platform-browser';
import { NgModule, NO_ERRORS_SCHEMA} from '@angular/core';
import {MDBBootstrapModule} from 'angular-bootstrap-md';
import {RouterModule, Routes} from '@angular/router';
import {FormsModule} from '@angular/forms';

import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { CarServiceComponent } from './car-service/car-service.component';
import { LaundryServiceComponent } from './laundry-service/laundry-service.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { WhyUsComponent } from './why-us/why-us.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { BookCarServiceComponent } from './car-service/book-car-service/book-car-service.component';

const appRoutes: Routes = [
  {path: 'home', component: HomeComponent},
  {path: 'whyus', component: WhyUsComponent},
  {path: 'carService', component: CarServiceComponent},
  {path: 'carservice/book', component: BookCarServiceComponent},
{path: 'laundryService', component: LaundryServiceComponent},
{path: 'contactUs', component: ContactUsComponent},
{path: 'login', component: LoginComponent},
{path: 'register', component: RegisterComponent},
{path: '', redirectTo: '/', pathMatch: 'full'}
];

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    CarServiceComponent,
    LaundryServiceComponent,
    ContactUsComponent,
    WhyUsComponent,
    HomeComponent,
    LoginComponent,
    RegisterComponent,
    BookCarServiceComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    RouterModule.forRoot(appRoutes),
    MDBBootstrapModule.forRoot()
  ],
  schemas: [NO_ERRORS_SCHEMA],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
